import { Component, OnInit } from '@angular/core';
import { Router ,NavigationEnd } from '@angular/router'; 
import { environment } from '../../../environments/environment';
@Component({
  selector: 'BP-bp-content-view',
  templateUrl: './bp-content-view.component.html',
  styleUrls: ['./bp-content-view.component.css']
})
export class BpContentViewComponent implements OnInit {

  contentPageUrl : any ;
  public magazineId :any;
  constructor(private router:Router) {
    this.magazineId = environment.magazineId ;
       router.events.subscribe(event => {
        if (event instanceof NavigationEnd ) {
          this.contentPageUrl = event.url;         
        }
      });


   }

  ngOnInit() {
  }

}
